import styles from './SupportProject.module.scss';
import { Link } from 'react-router-dom';
import Button from '../../UI/Button/Button';
import RadioButton from '../../UI/RadioButton/RadioButton';

export default function SupportProject() {
    return (
        <div className={styles.container}>
            <div className={styles.content}>
                <div className={styles.titleBlock}>
                    <div className={styles.title}> </div>
                    <div className={styles.titleText}>
                              
                              
                         
                    </div>
                </div>
                <div className={styles.buttons}>
                    <RadioButton text='300 ' width={100} height={30} />
                    <RadioButton text='500 ' width={100} height={30} />
                    <RadioButton text='1000 ' width={100} height={30} />
                    <RadioButton text='' width={200} height={30} />
                </div>
                <div className={styles.blockText}>
                    <div>
                        <input
                            type='email'
                            placeholder=' e-mail()'
                        />
                    </div>
                    <textarea
                        placeholder=' '
                        rows='7'
                        cols='65'
                    ></textarea>
                </div>
                <div className={styles.next}>
                    <Link to='/lost/lostFifth'>
                        <Button text='' width={150} height={34} />
                    </Link>
                </div>
            </div>
        </div>
    );
}
