import RadioButton from '../../UI/RadioButton/RadioButton';
import Button from '../../UI/Button/Button';
import styles from './LostFirst.module.scss';
import { Link } from 'react-router-dom';
import { useContext } from 'react';
// import { useState } from 'react'
import { ContextLost } from '../../pages/Lost/Context';

export default function LostFirst() {
    // const [firstLost, setFirstLost] = useState({
    //     animal: 'cat',
    //     gender: 'female'
    // });
    // console.log(formLost)

    const { formLost, formUpdate } = useContext(ContextLost);

    return (
        <div className={styles.container}>
            <div className={styles.textTitle}>
                <p>1/5:      </p>
            </div>
            <div className={styles.text}>
                <p>  (    )</p>
            </div>
            <div className={styles.buttons}>
                <RadioButton
                    text=''
                    name='animal'
                    value='dog'
                    width={110}
                    height={34}
                    formUpdate={formUpdate}
                    formLost={formLost}
                />
                <RadioButton
                    text=''
                    name='animal'
                    value='cat'
                    checked
                    width={110}
                    height={34}
                    formUpdate={formUpdate}
                    formLost={formLost}
                />
                <RadioButton
                    text=''
                    name='animal'
                    value=''
                    width={110}
                    height={34}
                    formUpdate={formUpdate}
                    formLost={formLost}
                />
            </div>
            <div className={styles.text}>
                <p> </p>
            </div>
            <div className={styles.buttons}>
                <RadioButton
                    text=''
                    name='gender'
                    value='male'
                    width={110}
                    height={34}
                    formUpdate={formUpdate}
                    formLost={formLost}
                />
                <RadioButton
                    text=''
                    name='gender'
                    value='female'
                    checked
                    width={110}
                    height={34}
                    formUpdate={formUpdate}
                    formLost={formLost}
                />
                <RadioButton
                    text=' '
                    name='gender'
                    value=''
                    width={110}
                    height={34}
                    formUpdate={formUpdate}
                    formLost={formLost}
                />
            </div>
            <div className={styles.next}>
                {/* <button onClick={() => formUpdate({animal: 'god', gender: 'ssss'})}></button> */}
                <Link to='/lost/lostSecond'>
                    <Button text='' width={150} height={34} />
                </Link>
            </div>
        </div>
    );
}
